import Vue from 'vue'
import App from './App.vue'
import './assets/css/styles.css'
import VueRouter from 'vue-router'
import {routes} from './router.js'

Vue.config.productionTip = false
Vue.use(VueRouter);

const router = new VueRouter({
  routes,
  mode:'history'
});

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
